body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-3xs: 10px;
  --font-size-xs: 12px;
  --font-size-7xl: 26px;

  /* Colors */
  --color-whitesmoke-100: #f4f8f5;
  --color-whitesmoke-200: #f0f4f3;
  --color-white: #fff;
  --color-gray: rgba(255, 255, 255, 0.2);
  --color-seagreen: #06a67e;
  --color-darkslategray: #373737;
  --color-darkgray: #9a9a9a;
  --color-mediumaquamarine: #38b593;
  --color-indianred: #e35e6a;

  /* border radiuses */
  --br-3xs: 10px;
  --br-xl: 20px;
}
